import { useState, useEffect, useCallback } from 'react';
import { toast } from 'react-toastify';
import { fetchUsers as apiFetchUsers, updateUserStatus as apiUpdateUserStatus } from '../api/api';

export const useUsers = (initialFilter = 'all') => {
  const [filter, setFilter]     = useState(initialFilter);
  const [users, setUsers]       = useState([]);
  const [isLoading, setLoading] = useState(false);

  // 1)   
  const loadUsers = useCallback(async () => {
    setLoading(true);
    try {
      const data = await apiFetchUsers(filter);
      setUsers(data);
    } catch (err) {
      toast.error(`   : ${err.message || ' '}`);
    } finally {
      setLoading(false);
    }
  }, [filter]);

  useEffect(() => {
    loadUsers();
  }, [loadUsers]);

  // 2)  :     +   
  const updateStatus = async (hotelId, status) => {
    try {
      await apiUpdateUserStatus(hotelId, status);
      //    
      setUsers(prev =>
        prev.map(u =>
          u.hotelId === hotelId
            ? { ...u, status }
            : u
        )
      );
      //  
      if (status === 'active') {
        toast.success(` ${hotelId}  `);
      } else {
        toast.success(` ${hotelId}  `);
      }
      return true;
    } catch (err) {
      if (status === 'active') {
        toast.error(` ${hotelId}  : ${err.message}`);
      } else {
        toast.error(` ${hotelId}  : ${err.message}`);
      }
      return false;
    }
  };

  return {
    users,
    filter,
    setFilter,
    isLoading,
    loadUsers,
    updateStatus,
  };
};
